<template>
	<div class="question-container">
		<div class="question-header">
			<div class="question-zhong">
				<div class="question-fanhui">
					<img src="../assets/nav_back.png" @click="back()">
				</div>
				<div class="question-ti">发帖子</div>
				<div class="question-fabu" @click="crt_discussion()">发布</div>
			</div>
		</div>
		<div class="question-content">
			<!--<div class="question-biaoti">标题</div>-->
			<div style="background: #fff;">
				<div style="border-top: 1px solid #EAEAEA;"></div>
				<div class="question-kuang">
					<div class="question-zi">
						<textarea v-model="title" placeholder="发布帖子的标题"></textarea>
					</div>
				</div>
				<div style="border-top: 1px solid #EAEAEA;"></div>
				<!--<div class="question-biaoti">问题描述</div>-->
				<div class="question-kuang1">
					<div class="question-wenzi">
						<textarea v-model="content" placeholder="请输入发帖的内容"></textarea>
					</div>
					<!--<div class="question-tianjia" @click="iMsg()">
						<img v-if="cardStatus == 1" src="../assets/btn_shangchuan.png">
						<img  v-for="( item , index ) in  imgpath" v-else :src="baseUrl+item">
					</div>-->
				</div>
			</div>
			
			<div class="question-flexf">
				<div class="question-tiwen">匿名</div>
				<div class="question-xuan"  @click="collect">
					<img src="../assets/dhao.png"  v-show="downIcon"/>
					<img src="../assets/ddhao.png" v-show="!downIcon"/>
				</div>
			</div>
			<div style="border-top: 1px solid #EAEAEA;"></div>
			<div class="question-tianjia" @click="iMsg()">
				<img v-if="cardStatus == 1" src="../assets/xiangce.png">
				<img  v-for="( item , index ) in  imgpath" v-else :src="baseUrl+item">
			</div>
			<!--<div class="question-tijiao" @click="crt_discussion()">提交</div>-->
			<!--<p >{{item}}</p>-->
		</div>
		
	</div>
</template>

<script>
	import Qs from 'qs'
	export default {
		name: 'home',
		data() {
			return {
				msg: '',
				user_info: {},
				data: {},
				title: '',
				content: '',
				downIcon: false,
				anonymity:'0',
				files: '',
				img_src: '',
				cardZ: '',
				cardStatus: '1',
				imgpath:[]
			}
		},
		methods: {
			collect() {
				//根据你的downIcon判断此时的排序是升序还是降序
				//...排序方式
				
				this.downIcon = !this.downIcon
			},
			back() {
				window.history.back();
			},
			//			安卓
			iMsg(){
				var that = this
				dsBridge.call('jsTakeUploads',that.user_info.user_id);
				dsBridge.register('addManyImg', function(r) {
//					alert(r)
					that.imgpath = r
//					alert( that.imgs[0] )
					that.cardStatus = 2
					var params = {
						userid: that.user_info.user_id,
						files: r
					};
					that.$http.post('/upload/listpic', Qs.stringify(params), {
							
					}).then((res) => {
						if(res.meta.code == 200) {
//							that.img_src = res.data;
						}
					})
				})
			},
			crt_discussion() {
				var that = this;
				if( that.downIcon == true ){
					that.anonymity = 0
				}else{
					that.anonymity = 1
				}
//				console.log( that.anonymity )
//				alert("1111")
				var params = {
					userid: that.user_info.user_id,
					title: that.title,
					content: that.content,
					anonymity:that.anonymity,
					pic: that.imgpath.toString()
				};
				that.$http.post('/discussion/crt_discussion', Qs.stringify(params), {

				}).then((res) => {
					if(res.meta.code == 200) {
						that.title = '',
						that.content = '',
						that.imgpath = '',
						that.$toast.success('提交成功');
						that.back()
						//						that.special_list = res.data;
					}else {
						that.$alert('提交失败', '提示');
					}
				})
			}
		},
		created() {
			// 页面刚加载的时候执行
			if(!!localStorage.getItem('user_info')) {
				this.user_info = JSON.parse(localStorage.getItem('user_info'));
			}
		},
		mounted() {
			// 页面刚加载完 dom 也加载完 的时候执行

		}
	}
</script>

<style scoped>
	.question-container {
		width: 100%;
		height: 100%;
		/*background: #FFFFFF;*/
	}
	
	.question-header {
		height: 50px;
		background: #FFFFFF;
		line-height: 50px;
		/*border-bottom: 1px solid #eaeaea;*/
	}
	
	.question-zhong {
		display: flex;
		justify-content: space-between;
	}
	
	.question-fanhui img {
		width: auto;
		height: 1.3rem;
		margin-left: 1.5rem;
		margin-top: 1rem;
	}
	
	.question-ti {
		font-size: 1.8rem;
		color: #000000;
		/*margin-left: -1.5rem;*/
	}
	.question-fabu{
		font-size: 1.5rem;
		color: #fc5d98;
		font-weight: 550;
		padding-right: 1rem;
	}
	.question-content {
		width: 100%;
		height: calc(100% - 50px);
		overflow-x: hidden;
		overflow-y: auto;
	}
	
	.question-biaoti {
		font-size: 1.4rem;
		color: #000000;
		padding: 1.7rem 1.5rem;
	}
	
	.question-kuang {
		/*border: 1px solid #EAEAEA;*/
		border-radius: 5px;
		background: #fff;
		height: 4rem;
		margin: 0 1.5rem;
	}
	
	.question-zi textarea {
		font-size: 1.6rem;
		width: 100%;
		height: 3rem;
		/*padding: 0 1.5rem;*/
		background: #fff;
		border: none;
		padding-top: 1rem;
		outline: none;
	}
	
	.question-wenzi textarea {
		width: 100%;
		height: 35rem;
		/*padding: 0 1.5rem;*/
		background: #fff;
		border: none;
		outline: none;
		padding-top: 1rem;
	}
	
	.question-kuang1 {
		/*border: 1px solid #f2f2f2;*/
		border-radius: 5px;
		background: #fff;
		/*height: 22rem;*/
		margin: 0 1.5rem;
	}
	.question-tianjia{
		width: 100%;
		margin: 0 auto;
		background: #F5F5F5;
		padding-bottom: 3rem;
	}
	.question-tianjia img {
		width: 2.3rem;
		height: 2rem;
		margin: 1rem;
		
		/*position: relative;
		top: -7rem;
		left: 0.5rem;*/
	}
	
	.question-flex {
		display: flex;
	}
	
	.question-xuan img {
		width: auto;
		height: 1.5rem;
		margin-left: 1.5rem;
		margin-top: 1rem;
	}
	
	.question-tiwen {
		font-size: 1.5rem;
		color: #000000;
		padding: 0.9rem 1.5rem 1.6rem 0.5rem;
	}
	
	.question-tijiao {
		font-size: 1.8rem;
		border: 1px solid #8CADF5;
		background: #8CADF5;
		text-align: center;
		margin: 1rem 1.5rem;
		border-radius: 5px;
		padding: 0.5rem 0;
		color: #FFFFFF;
	}
	.question-flexf{
		display: flex;
		flex-direction: row-reverse;
		background: #F5F5F5;
	}
</style>


